<template>
<div>
    <app-mobile-search class="hidden-md-and-up"></app-mobile-search>
    <app-first></app-first>
    <v-divider/>
    <v-container v-if="$route.query.q" style="max-width: 960px">
        <app-category  :filterCategory="filterCategory" :categories="categories"></app-category>
        <app-portfolio-reuse :portfolios="portfolios" :loading="loading"></app-portfolio-reuse>
    </v-container>
</div>
</template>

<script>
    import appFirst from '../../components/search/First'
    import appCategory from '../../components/search/Category'
    import appMobileSearch from '../../components/search/MobileSearch'
    import appPortfolioReuse from '../../components/reuseComponents/Portfolio'

    export default {
        components: {
            appFirst,
            appMobileSearch,
            appPortfolioReuse,
            appCategory
        },
        data: () => ({
            portfolios: [],
            categories: [],
            loading: false,
        }),
        methods: {
           async search (cateId) {
                try {
                    if(cateId) {
                        this.loading = true
                        const results = await this.$axios.get('search', { params: { search: this.$route.query.q, categoryId: cateId }
                        })
                        this.portfolios = results.data.portfolios
                        setTimeout(() => {
                            this.loading = false
                        }, 200)
                    } else {
                        this.loading = true
                        const results = await this.$axios.get('search', { params: { search: this.$route.query.q }
                        })
                        this.portfolios = results.data.portfolios
                        setTimeout(() => {
                            this.loading = false
                        }, 200)
                    }
                } catch (e) {
                    throw new Error(e)
                }
            },
            async fetchCategories () {
                try {
                    const results = await this.$axios.get('category/getall')
                    const categories = results.data.categories
                    categories.unshift({ name: 'ທັງໝົດ', _id: 'all' })
                    this.categories = categories
                } catch (e) {
                    throw new Error(e)
                }
            },
            async filterCategory (cateId) {
                try {
                    if(cateId === 'all') {
                        await this.search()
                    } else {
                       await this.search(cateId)
                    }
                } catch (e) {
                    throw new Error(e)
                }
            }
        },
        watch: {
            '$route.query' : {
                handler () {
                        if(this.$route.query.q) {
                            this.search()
                        } else {
                            this.portfolios = null
                            this.loading = false
                        }
                },
                immediate: true
            }
        },
        created () {
            this.fetchCategories()
            if(this.$route.query.q) {
                this.search()
            }
        }
    }
</script>
