<template>
    <v-row no-gutters>
        <v-col
                class="mr-auto"
                cols="4" xs="4" sm="4" md="2" lg="2"
        >
                      <v-select
                              tile
                              dense
                              outlined
                              solo
                              color="blue darken-1"
                              @change="filterCategory(cateId)"
                              v-model="cateId"
                              :items="categories"
                              item-text="name"
                              item-value="_id"
                              style="font-size: 13px"
                      ></v-select>
        </v-col>
    </v-row>
</template>

<script>
    export default {
        data: () => ({
           cateId: 'all',
           reviewScore: 'all'
        }),
        props: [
            'categories',
            'filterCategory'
        ],
        watch: {
            '$route.query': {
                handler (change) {
                    if(change) {
                        this.cateId = 'all'
                    }
                }
            }
        }
    }
</script>
